import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

//import { environment } from '@environments/environment';
//import { Dispositivo } from '@app/_models';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { Dispositivo } from '../_models';
import { environment } from 'src/environments/environment.prod';

@Injectable({ providedIn: 'root' })
export class DispositivosService {
    constructor(private http: HttpClient) { }

    getAll() : Observable<Dispositivo[]> {
        return this.http.get<Dispositivo[]>(`${environment.apiUrl}/control/dispositivos`)
        .pipe(map(dispositivos => {
            dispositivos.forEach(disp=>{disp.dispositivoId = disp.id.dispositivoId});
            return dispositivos;
        }));
    }
}