import { Component, OnInit, Input } from '@angular/core';
import { Dispositivo } from '../_models';
import { WebsocketService, AuthenticationService, Status } from '../_services';
//import {  Dispositivo } from '@app/_models';
//import { WebsocketService, Status, AuthenticationService } from '@app/_services';

@Component({
  selector: 'iot-dispositive',
  templateUrl: './dispositive.component.html',
  styleUrls: ['./dispositive.component.css']
})
export class DispositiveComponent implements OnInit {
  
  
  @Input()
  dispositivo:Dispositivo;

  sinSombra:boolean = true;

  loading = false;

  greetings: string[] = [];
 
  constructor(private auth:AuthenticationService, private websocket:WebsocketService) { 
  }
 
  suscribierse(){

    this.websocket.subscribe(`/user/thing/${this.dispositivo.dispositivoId}/reported`).subscribe(reportedStatus => {
      this.reported(reportedStatus);
    },err => this.disconnect(err));
    
    this.websocket.subscribe(`/thing/${this.dispositivo.dispositivoId}/reported`).subscribe(reportedStatus => {
      this.reported(reportedStatus);
    },err => this.disconnect(err));

    this.websocket.subscribe(`/user/thing/errors`).subscribe(reportedStatus => {
      this.error(reportedStatus);
    },err => this.disconnect(err));
    
    this.websocket.subscribe(`/user/thing/${this.dispositivo.dispositivoId}/desired/response`).subscribe(reportedStatus => {
      //console.log(reportedStatus.body);
      this.showGreeting(reportedStatus.body);
    },err => this.disconnect(err));
   
  }

  private reported(reportedStatus){
    this.loading = false;
    console.log(this.dispositivo.dispositivoId, reportedStatus.body);
    if (!reportedStatus.body) {
      return;
    }
    if(reportedStatus.body.includes("state")){
      let mensaje = JSON.parse(reportedStatus.body);
      console.log(reportedStatus.body);
                
      if(mensaje.state.reported == undefined ){
        return;
      }
      console.log(mensaje.state.reported.status);
      this.dispositivo.status = mensaje.state.reported.status;
    }else{
      //Para los status que no utilizan la sombra
      let mensaje = JSON.parse(reportedStatus.body);
     	console.log(reportedStatus.body);
       console.log(mensaje.status);
       this.dispositivo.status = mensaje.status;
    }     
    this.sinSombra = false;
  }

  private error(reportedStatus){
    console.error("Mostrando errores en interfaz");
      console.info(reportedStatus.body);
      console.info(JSON.parse(reportedStatus.body).message, JSON.parse(reportedStatus.body).error);
      this.showGreeting(JSON.parse(reportedStatus.body).message);
  }
 
  disconnect(err) {
    //TODO Revisar el error
    console.error(err);
    if(err=="invalid_token"){
      this.showGreeting("Se termino la sesion, volver a conectarse");
    }
    this.websocket.disconnect();

    this.auth.logout();
    location.reload(true);
  }

  changeStatus(){
    if(this.loading){
      return;
    }
    let statusTemp = this.dispositivo.status;

    setTimeout(()=>{ //Para garantizar que no se quede en status desired
      console.warn("Revisar status", this.dispositivo.status,  this.sinSombra);
      /*if(this.dispositivo.status == "DESIRED" && this.sinSombra){
        this.dispositivo.status = statusTemp=="ON"?"OFF":"ON";
        console.warn("Sincronizando ");
      }*/
      this.loading = false;
      
    }, 6000);
    this.loading = true;
    if(this.dispositivo.status == "DESIRED"){//Random entre los status para sincronizarse
      let status = ["ON", "OFF"][Math.floor(Math.random()*2)]
      this.dispositivo.status = status;
      console.log(status);
    }else{
      this.dispositivo.status = this.dispositivo.status=="ON"?"OFF":"ON";
    }
    
    try{
      this.websocket.send(this.dispositivo);
    }catch(e){
      console.error(e);
    }
    this.dispositivo.status = "DESIRED";
  }
  
 
  reconectar() {
    if(this.websocket.status != Status.CONECTADO){
      this.connectar();
    }else{
      this.showGreeting("Ya se encuentra conectado");
    }
  }
 
  showGreeting(message) {
    console.log(message);
    //this.greetings.push(message);
  }

  connectar(){
    this.websocket.connect(this.auth.currentUserValue).then( ws => {
      console.log("Componente conectado");
      this.suscribierse();
      this.websocket.updateStatus(this.dispositivo);
    }).catch(error => {
      console.log("No se pudo conectar el componente", error);
      this.showGreeting("No se pudo conectar al servidor, revise sus datos");
    })
  }

  ngOnInit(): void {
    if(this.dispositivo && !this.dispositivo.status){
      console.log("Status cambiado a lo wey")
    }
    this.connectar();
  }


}


