export class Usuario {
    constructor() {
        this.token = new Token();
    }
    usuario: string;
    id: number;
    password: string;
    activo: boolean;
    token: Token;
}

export class Token{
    access_token: string;
    token_type: string;
    refresh_token: string;
    expires_in: number;
    scope: string;
    user_id: number;
    error: string;
    error_description: string;
}
