import { Component, Injectable, OnDestroy, OnInit } from '@angular/core';
import * as SockJS from 'sockjs-client';
import * as Stomp from '@stomp/stompjs';
import { CompatClient, messageCallbackType, IMessage } from '@stomp/stompjs';
import { Observable, VirtualTimeScheduler } from 'rxjs';
//import { environment } from '@environments/environment';
//import { Usuario, Dispositivo } from '@app/_models';
import { AuthenticationService } from './authentication.service';
import { Usuario, Dispositivo } from '../_models';
import { environment } from 'src/environments/environment.prod';


Injectable({
  providedIn: 'root' 
})
export class WebsocketService implements OnDestroy  {

  public status:Status = Status.DESCONECTADO;
  private stompClient:CompatClient = null;
  private promesa:Promise<WebsocketService>;
  private currentUser: Usuario;

  
  constructor() {}

 
 
  public connect(usuario: Usuario):Promise<WebsocketService> {
    this.currentUser = usuario;
    const socket = new SockJS(`${environment.websocketUrl}?access_token=${this.currentUser.token.access_token}`);
    const _this = this;
    
    if(this.status == Status.CONECTADO){
      return new Promise<WebsocketService> (function(resolve,reject) {
        resolve(_this);
      });
    }else if(this.status == Status.CONECTANDO){
      return this.promesa;
    }

    this.status = Status.CONECTANDO;
    
    
    this.stompClient = Stomp.Stomp.over(socket);

    this.stompClient.heartbeat.outgoing = 20000; // client will send heartbeats every 20000ms
    this.stompClient.heartbeat.incoming = 0
    
    this.promesa = new Promise<WebsocketService> (function(resolve,reject) {
        _this.stompClient.connect({}, function (frame) {
          _this.status = Status.CONECTADO;
          //console.log('Connected: ' + frame);
          this.reconnect_delay = 5000;
          resolve(_this);
        }, function(error){
          console.log("Ocurrio un error al intentar conectar el socket", error);
          _this.status = Status.ERROR; 
          reject("No fue posible conectarse");
        }, function(error){
          console.log("Ocurrio un error al intentar conectar el socket", error);
          _this.status = Status.ERROR;
          reject(error.reason);
        });
      
    });
 
    return this.promesa;
    
  }
 
  disconnect() {
    if (this.stompClient != null) {
      this.stompClient.disconnect();
    }
    this.status = Status.DESCONECTADO;
  }
 
  send(data:Dispositivo) {
    if(this.status == Status.CONECTADO){
      this.stompClient.send('/desired',{},JSON.stringify(data));
    }else{
      throw "No esta conectado al servidor"
    }
  }

  updateStatus(data:Dispositivo) {
    if(this.status == Status.CONECTADO){
      this.stompClient.send("/status/update/"+data.dispositivoId);
    }else{
      throw "No esta conectado al servidor"
    }
  }

  ngOnDestroy(): void {
    this.disconnect();
  }

  subscribe(topic:string ):Observable<IMessage>{
    return new Observable(observer => {
      if(this.status == Status.CONECTADO){
        try{
          this.stompClient.subscribe(topic, (response)=>{
            if(response.body.includes("invalid_token")){
              observer.error("invalid_token");
            }
            observer.next(response);
          });
        }catch(e){
          console.error("No se pudo conectar a "+topic, e);
          observer.error(e);
        }
      }else{
        console.error("No hay conexión"+topic);
        observer.error("No hay conexión"+topic);
      }
    });
    
  }

  
}

export const enum Status {
  DESCONECTADO,
  CONECTANDO,
  CONECTADO, 
  ERROR
}