import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { Usuario, Token } from '../_models';
import { environment } from 'src/environments/environment.prod';

//import { environment } from '@environments/environment';
//import { Usuario, Token } from '@app/_models';

@Injectable({ providedIn: 'root' })
export class AuthenticationService {
    private currentUserSubject: BehaviorSubject<Usuario>;
    public currentUser: Observable<Usuario>;

    constructor(private http: HttpClient) {
        this.currentUserSubject = new BehaviorSubject<Usuario>(JSON.parse(localStorage.getItem('currentUser')));
        this.currentUser = this.currentUserSubject.asObservable();
    }

    public get currentUserValue(): Usuario {
        return this.currentUserSubject.value;
    }

    login(username: string, password: string):Observable<Usuario> {

        let headers =  { "Authorization": "Basic " + btoa("macucoapp:secret") };
        let params = {'grant_type':'password', 'username':username, 'password':password};
    
    return this.http.post<Token>(`${environment.apiUrl}/oauth/token`, null, { headers , params })
        .pipe(map(token => {
            // store user details and jwt token in local storage to keep user logged in between page refreshes
            let user = new Usuario();
            user.token = token;
            user.id = token.user_id;
            user.usuario = username;
            localStorage.setItem('currentUser', JSON.stringify(user));
            this.currentUserSubject.next(user);
            return user;
        }));
/*
        return this.http.post<any>(`${environment.apiUrl}/users/authenticate`, { username, password })
            .pipe(map(user => {
                // store user details and jwt token in local storage to keep user logged in between page refreshes
                localStorage.setItem('currentUser', JSON.stringify(user));
                this.currentUserSubject.next(user);
                return user;
            }));
            */
    }

    logout() {
        // remove user from local storage to log user out
        localStorage.removeItem('currentUser');
        this.currentUserSubject.next(null);
    }
}