import { Component } from '@angular/core';
import { first } from 'rxjs/operators';
import { Dispositivo } from '../_models';
import { DispositivosService } from '../_services';

//import { Dispositivo } from '@app/_models';
//import { DispositivosService } from '@app/_services';

@Component({ templateUrl: 'home.component.html' })
export class HomeComponent {
    loading = false;
    dispositivos: Dispositivo[];

    constructor(private dispositivosService: DispositivosService) { }

    ngOnInit() {
        this.loading = true;
        this.dispositivosService.getAll().pipe(first()).subscribe(disp => {
            this.loading = false;
            this.dispositivos = disp;
        });
    }
}